.table{border-spacing:0;border-collapse:collapse}.table>thead{background-color:#f6f4f4}.table>thead>tr>th{text-align:left;width:360px;padding:0 24px;height:44px}.table>thead>tr>th:nth-child(4){width:80px}.table>thead>tr>th:nth-child(1){cursor:pointer}.table>tbody>tr>td{text-align:left;padding:0 24px;height:68px;border-bottom:1px solid #dfdddd}.table__edit{cursor:pointer;padding:6px;border-radius:6px;border:1px solid #f2eeee;width:32px;height:32px}.table__name::after{content:" ▵";cursor:pointer}.table__name__active::after{content:" ▿";cursor:pointer}